var CELLS, Cell, CellStore, Dispatcher, EventEmitter, GAME, GRID, INITIALIZED, Immutable, KeyDownAction, Ticker, XY, last_cells, ref;

KeyDownAction = require('../actions').KeyDownAction;

ref = require('../utility'), Ticker = ref.Ticker, XY = ref.XY, GAME = ref.GAME;

Cell = require('../views/cell');

GRID = require('../settings').GRID;

Dispatcher = require('../dispatcher');

EventEmitter = require('events').EventEmitter;

Immutable = require('immutable');

CELLS = Immutable.OrderedMap().withMutations(function(mutative_cells) {
  var col, i, len, range, results, row;
  range = GRID.range();
  results = [];
  for (i = 0, len = range.length; i < len; i++) {
    row = range[i];
    results.push((function() {
      var j, len1, results1;
      results1 = [];
      for (j = 0, len1 = range.length; j < len1; j++) {
        col = range[j];
        results1.push(mutative_cells.set(XY.value_of(row, col), null));
      }
      return results1;
    })());
  }
  return results;
});

last_cells = null;

INITIALIZED = false;

CellStore = Object.create(EventEmitter.prototype, {
  initialize: {
    enumerable: true,
    value: function() {
      if (INITIALIZED) {
        throw new Error("CellStore already initialized");
      }
      this._reset();
      return Dispatcher.register((function(_this) {
        return function(action) {
          return _this._handle_action(action);
        };
      })(this));
    }
  },
  cellmap: {
    enumerable: true,
    value: function() {
      return CELLS;
    }
  },
  add_change_listener: {
    enumerable: true,
    value: function(listener) {
      return this.addListener(this.CHANGE_EVENT, listener);
    }
  },
  CHANGE_EVENT: {
    enumerable: true,
    value: 'change'
  },
  METHOD_KEYMAP: {
    value: Immutable.Map([['restart', '__restart']])
  },
  _handle_action: {
    value: function(action) {
      var method, motion;
      if (action.is(KeyDownAction)) {
        motion = action.motion();
        method = action.method();
        if ((motion != null) && !GAME.game_over()) {
          GAME.set_motion(motion);
          if (!Ticker.ticking()) {
            return this._tick();
          }
        } else if (method != null) {
          return this[this.METHOD_KEYMAP.get(method)]();
        }
      }
    }
  },
  _tick: {
    value: function() {
      last_cells = CELLS;
      this._update();
      if (GAME.out_of_bounds()) {
        GAME.collide(Cell.Wall);
      }
      if (GAME.game_over()) {
        this._finish();
      } else {
        Ticker.tick((function(_this) {
          return function() {
            return _this._tick();
          };
        })(this));
      }
      return this.emit(this.CHANGE_EVENT, {
        cellmap: CELLS
      });
    }
  },
  _reset: {
    value: function() {
      var random_cell;
      random_cell = function() {
        var cell;
        cell = Cell.random();
        if (cell === Cell.Item) {
          GAME.add_item();
        }
        return cell;
      };
      GAME.reset();
      CELLS = CELLS.withMutations(function(mutative_cells) {
        return mutative_cells.forEach(function(cell, xy) {
          if (GAME.collision(xy)) {
            return mutative_cells.set(xy, Cell.Snake);
          } else {
            return mutative_cells.set(xy, random_cell());
          }
        });
      });
      return this.emit(this.CHANGE_EVENT, {
        cellmap: CELLS
      });
    }
  },
  _finish: {
    value: function() {
      var transform_to_cell;
      transform_to_cell = GAME.failed() ? Cell.Collision : Cell.Item;
      if (GAME.failed()) {
        this._rewind();
      }
      return CELLS = CELLS.withMutations(function(mutative_cells) {
        return mutative_cells.forEach(function(cell, xy) {
          if (cell === Cell.Snake) {
            return mutative_cells.set(xy, transform_to_cell);
          }
        });
      });
    }
  },
  _rewind: {
    value: function() {
      CELLS = last_cells;
      return last_cells = null;
    }
  },
  _update: {
    value: function() {
      GAME.move_snake();
      return CELLS = CELLS.withMutations((function(_this) {
        return function(mutative_cells) {
          return mutative_cells.forEach(function(previous_cell, xy) {
            if (GAME.collision(xy)) {
              if (previous_cell === Cell.Void) {
                return mutative_cells.set(xy, Cell.Snake);
              } else {
                if (previous_cell === Cell.Item) {
                  mutative_cells.set(xy, Cell.Snake);
                }
                return GAME.collide(previous_cell, xy);
              }
            } else if (previous_cell === Cell.Snake) {
              return mutative_cells.set(xy, Cell.Void);
            }
          });
        };
      })(this));
    }
  },
  __restart: {
    value: function() {
      return Ticker.stop((function(_this) {
        return function() {
          return _this._reset();
        };
      })(this));
    }
  }
});

module.exports = CellStore;
